:root {
  --global-primary-color-dark: #e38b29;
  --global-primary-color-main: #f1a661;
  --global-primary-color-medium: #ffd8a9;
  --global-primary-color-light: #fdeedc;

  --global-secondary-color-dark: #f4a261;
  --global-secondary-color-main: #949828;
  --global-secondary-color-medium: #949828;
  --global-secondary-color-light: #f4a261;

  --global-color-grey-dark: #202020;
  --global-color-grey-medium: #5a5a5a;
  --global-color-grey-medium-light: #bebebe;
  --global-color-grey-light: #f0f0f0;

  --global-border-radius: 6px;
  --global-padding: 12px;
  --global-margin: 12px;

  --global-font-size-xl: 30px;
  --global-font-size-l: 20px;
  --global-font-size-m: 16px;
  --global-font-size-s: 14px;

  --global-font-weight-xl: 900;
  --global-font-weight-l: 700;
  --global-font-weight-m: 500;
  --global-font-weight-s: 400;

  --global-navbar-width: 240px;
  --global-headerbar-height: 60px;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.wrapper {
  width: 100%;
  height: 100%;
  overflow: scroll;
}

.content {
  position: relative;
  height: 150vh;
  width: calc(1080px * 2);
  background-color: white;
  background-size: 30px 30px;
  background-image: radial-gradient(circle, #b8b8b8bf 1px, rgba(0, 0, 0, 0) 1px);
}

body{
  background-color: #343434;
}

.node {
  display: flex;
  flex-direction: column;
  position: absolute;
  cursor: grab;
  background-color: white;
  border: 1px solid #e6d4be;
  border-radius: 6px;
  box-shadow: 1px 1px 11px -6px rgba(0, 0, 0, 0.75);
  user-select: none;
  z-index: 1;
  transition: border ease 0.2s, box-shadow ease 0.2s;
}

.node:hover {
  box-shadow: 2px 2px 12px -6px rgba(0, 0, 0, 0.75);
}

.nodeSelected {
  display: flex;
  flex-direction: column;
  position: absolute;
  cursor: grab;
  background-color: white;
  border: 1px solid #e38c29;
  border-radius: 6px;
  box-shadow: 1px 1px 11px -6px rgba(0, 0, 0, 0.75);
  user-select: none;
  z-index: 100;
  transition: border ease 0.2s, box-shadow ease 0.2s;
}

.nodeSelected:hover {
  box-shadow: 2px 2px 12px -6px rgba(0, 0, 0, 0.75);
}

.nodeLabel {
  padding: 12px;
  border-bottom: 1px solid #f0f0f0;
}

.nodeContent {
  padding: 12px;
}

.nodeInputs {
  pointer-events: none;
  cursor: initial;
  z-index: -3;
  position: absolute;
  top: 0px;
  left: calc(12px * -1.5);
  display: flex;
  flex-direction: column;
  margin: 12px 0px;
}

.nodeInput {
  pointer-events: all;
  cursor: initial;
  background-color: #e38b29;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  margin: 12px 0px;
  box-shadow: 1px 1px 11px -6px rgba(0, 0, 0, 0.75);
}

.nodeOutputs {
  pointer-events: none;
  z-index: -3;
  position: absolute;
  top: 0px;
  right: calc(12px * -1.5);
  display: flex;
  flex-direction: column;
  margin: 12px 0px;
}

.nodeOutput {
  pointer-events: all;
  cursor: crosshair;
  background-color: #e38b29;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  margin: 12px 0px;
  box-shadow: 1px 1px 11px -6px rgba(0, 0, 0, 0.75);
}

.actions {
  pointer-events: none;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  top: -30px;
  right: 0px;
  opacity: 1;
  transition: all ease 0.2s;
}

.actionsHidden {
  pointer-events: none;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 0px;
  top: -30px;
  right: -12px;
  opacity: 0;
  overflow: hidden;
  transition: all ease 0.2s;
}

.delete {
  width: 24px;
  height: 24px;
  fill: #a11111;
  border-radius: 100%;
  pointer-events: all;
  cursor: pointer;
  margin-right: 6px;
  opacity: 1;
  transition: all ease 0.2s;
}

.delete {
  pointer-events: all;
}

.icon {
  width: 100px;
  height: 100px;
  background-color: white;
  fill: white;
}

.circle {
  cx: 0;
  cy: 0;
  r: 12;
  fill: rgba(168, 168, 168, 1);
}

.edge {
  pointer-events: all;
  stroke: rgba(168, 168, 168, 0.8);
  stroke-width: 2;
  fill: transparent;
  cursor: pointer;
}

.edgeSelected {
  stroke: rgba(168, 168, 168, 1);
  stroke-width: 3;
  fill: transparent;
  z-index: 100;
}

.edgeNew {
  stroke: rgba(168, 168, 168, 0.4);
  stroke-width: 2;
  fill: transparent;
}
